/* eslint-disable @typescript-eslint/no-non-null-assertion */
import inspector from "inspector"
import { IGatsbyNode } from "./redux/types"
import onExit from "signal-exit"
import { ChildProcess } from "child_process"
import reporter from "./reporter"
import { GatsbyConfig } from "./types"
import { ControllableScript } from "./utils/controllable-script"
import crypto from "crypto"
import { ExecutionResult } from "graphql"
import { existsSync, rm } from "fs-extra"
import path from "path"
import { FrameworkContext } from "./framework-hooks/index"
import { resolveFromContentEngine } from "./core-utils/create-require-from-path"
import { IDataStore } from "./datastore/types"
import { getDefaultDbPath, getLmdbStore } from "./datastore/lmdb/lmdb-datastore"

export * from "./types"
export { setFrameworkHook } from "./framework-hooks/index"

export interface ContentEngineConfiguration {
  directory?: string
  verbose?: boolean
  openTracingConfigFile?: string
  host?: string
  port?: number | string
  engineConfig?: GatsbyConfig
  env?: Record<string, string>
  frameworkHooks?: string

  // default is true
  runInSubProcess?: boolean
}

type InitializeOptions = {
  clearCache?: boolean
  context?: Record<string, unknown>
} & Pick<ContentEngineConfiguration, "env">

type SyncOptions = InitializeOptions & {
  runServer?: boolean
  webhookBody?: { [key: string]: any }
  connector?: string

  buildSchema?: true | boolean
}

export interface SyncState {
  exitCode?: number
  exited?: boolean
  error?: {
    message: string
    stack: string
  }
}

export interface DataStore
  extends Pick<IDataStore, "getNode" | "getTypes" | "countNodes" | "runQuery"> {
  getNodes: IDataStore["iterateNodes"]
  getNodesByType: IDataStore["iterateNodesByType"]
}
export interface ContentEngine {
  sync: (options?: SyncOptions) => Promise<SyncState>
  initialize: (options?: InitializeOptions) => Promise<void>
  restart: (options?: SyncOptions) => Promise<SyncState>
  stop: () => Promise<void>
  config: (
    config: ContentEngineConfiguration
  ) => Promise<ContentEnginePublicAPI>
  onStdOut: (callback: (data: string) => void) => void
  onStdErr: (callback: (data: string) => void) => void
  onMessage: (callback: (message: any) => void) => void
  sendMessage: (message: any) => void
  clearListeners: () => void
  /**
   * Test utils, only available when process.env.NODE_ENV === `test`
   */
  test: {
    query: <TData>(
      query: string,
      variables?: Record<string, unknown>
    ) => Promise<ExecutionResult<TData>>
    getNodes: <NodeFields>() => Promise<Array<IGatsbyNode & NodeFields>>
    getNode: <NodeFields>(id: string) => Promise<IGatsbyNode & NodeFields>
    getNodesByType: <NodeFields>(
      type: string
    ) => Promise<Array<IGatsbyNode & NodeFields>>
  }
  store?: DataStore
  getProcess: () => ChildProcess
}

export type ContentEnginePublicAPI = ContentEngine

export const contentEngine = (
  engineOptions?: ContentEngineConfiguration
): ContentEnginePublicAPI => {
  const { directory, frameworkHooks } = engineOptions || {}

  if (directory && !existsSync(directory)) {
    throw new Error(`Content Engine directory does not exist: ${directory}`)
  }

  if (frameworkHooks && typeof frameworkHooks !== `string`) {
    throw new Error(
      `The framework file passed to contentEngine({ frameworkHooks: "..." }) must be a string.`
    )
  }

  if (frameworkHooks && !existsSync(frameworkHooks)) {
    throw new Error(
      `The framework file passed to contentEngine({ frameworkHooks: "${frameworkHooks}" }) does not exist. Make sure the path to the framework file is correct.`
    )
  }

  engineOptions = {
    directory: process.cwd(),
    ...(engineOptions || {}),
  }

  if (
    `runInSubProcess` in engineOptions &&
    engineOptions.runInSubProcess === false
  ) {
    process.chdir(engineOptions.directory!)
    const contentEngineDir = resolveFromContentEngine(
      `/services/content-engine`,
      engineOptions?.directory
    )

    if (
      !contentEngineDir ||
      (contentEngineDir && !existsSync(contentEngineDir))
    ) {
      throw new Error(
        `Content Engine directory does not exist: ${contentEngineDir}`
      )
    }

    const { contentEngine } = require(contentEngineDir)

    return contentEngine(engineOptions)
  }

  const state: SyncState = {
    exited: false,
  }
  const internalState: {
    subProcess?: ControllableScript
    env: ContentEngineConfiguration["env"]
    processListenersWereSet: boolean
    stdErrListeners: Array<(data: string) => void>
    stdOutListeners: Array<(data: string) => void>
    messageListeners: Array<(message: any) => void>
    store?: IDataStore
  } = {
    env: engineOptions?.env || {},
    processListenersWereSet: false,
    stdErrListeners: [],
    stdOutListeners: [],
    messageListeners: [],
  }

  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  function onceReceived(message: string): Promise<any> {
    return new Promise(res => {
      const subprocess = internalState.subProcess as ControllableScript

      if (!subprocess) {
        new Error(`Content Engine process is not running but should be.`)
      }

      const exitListener = () => {
        // @ts-ignore if subProcess is accessed from outside it will be recreated automatically. setting it to null here allows it to be recreated as needed
        internalState.subProcess = null
        res(false)
      }

      const listener = receivedMessage => {
        if (receivedMessage.type === message) {
          subprocess.offMessage(listener)
          subprocess.offExit(exitListener)
          res(receivedMessage.payload || true)
        }
      }

      subprocess.onMessage(listener)
      subprocess.onExit(exitListener)
    })
  }

  const createSubProcessIfNoneExists = async ({
    env,
  }: {
    env: ContentEngineConfiguration["env"]
  }): Promise<{
    isRunning: boolean
  }> => {
    if (!internalState.subProcess) {
      state.exited = false
      state.exitCode = undefined
      internalState.env = env || internalState.env

      reporter.verbose(`[content-engine] starting subprocess`)

      const contentEngineDir =
        resolveFromContentEngine(
          `/services/content-engine`,
          engineOptions?.directory
        ) || require.resolve(`./services/content-engine`)

      const reduxDir =
        resolveFromContentEngine(`/redux`, engineOptions?.directory) ||
        require.resolve(`./redux`)

      internalState.subProcess = new ControllableScript(
        `
          const engineOptions = ${JSON.stringify(engineOptions)}
          const { contentEngine } = require("${contentEngineDir}")
          const { saveState } = require("${reduxDir}")

          const engine = contentEngine(engineOptions)

          if (!process.send) {
            throw new Error(
              'Started Content Engine as a subprocess, but no parent was found.'
            )
          }

          process.send({
            type: 'CONTENT_ENGINE_CHILD_RUNNING',
          })

          process.on('message', async message => {
            if (message.type === 'COMMAND' && message.action?.type === 'EXIT') {
              saveState()
              const code = typeof message.action?.payload === 'number'
                ? message.action.payload
                : 0

              process.exit(code)
            } else if (message.type === 'CONTENT_ENGINE_CHILD_SYNC_DATA') {
              engine.sync(message.payload).then(() => {
                process.send({
                  type: 'CONTENT_ENGINE_CHILD_FINISHED_SYNCING_DATA',
                })
              }).catch(e => {
                process.send({
                  type: 'CONTENT_ENGINE_CHILD_FINISHED_SYNCING_DATA',
                  payload: {
                    error: {
                      message: e.message,
                      stack: e.stack
                    }
                  }
                })
              })
            } else if (message.type === 'CONTENT_ENGINE_CHILD_QUERY') {
              engine.test.query(message.payload.query, message.payload.variables).then(result => {
                process.send({
                  type: 'CONTENT_ENGINE_CHILD_QUERY_RESULT--' + message.payload.queryId,
                  payload: {
                    result
                  }
                })
              }).catch(e => {
                console.error(e)
                process.send({
                  type: 'CONTENT_ENGINE_CHILD_QUERY_RESULT--' + message.payload.queryId,
                  payload: {
                    error: e.message,
                    stack: e.stack
                  }
                })
              })
            } else if (message.type === 'CONTENT_ENGINE_CHILD_INITIALIZE') {
              engine.initialize(message.payload).then(() => {
                process.send({
                  type: 'CONTENT_ENGINE_CHILD_FINISHED_INITIALIZING',
                })
              }).catch(e => {
                process.send({
                  type: 'CONTENT_ENGINE_CHILD_FINISHED_INITIALIZING',
                  error: {
                    message: e.message,
                    stack: e.stack
                  }
                })
              })
            } else if (message.type === 'CONTENT_ENGINE_CHILD_INVOKE_TEST_UTIL') {
              const responseType = 'CONTENT_ENGINE_CHILD_INVOKE_TEST_UTIL_RESULT--' + message.payload.messageId

              function sendError(e) {
                console.error(e)
                process.send({
                  type: 'CONTENT_ENGINE_CHILD_INVOKE_TEST_UTIL_RESULT--' + message.payload.messageId,
                  payload: {
                    error: e.message,
                    stack: e.stack
                  }
                })
              }

              try {
                const promiseOrResult = engine.test[message.payload.utilName](...message.payload.args)

                if (promiseOrResult && 'then' in promiseOrResult) {
                  promiseOrResult.then(result => {
                    process.send({
                      type: responseType,
                      payload: {
                        result
                      }
                    })
                  }).catch(sendError)
                } else {
                  process.send({
                    type: responseType,
                    payload: {
                      result: promiseOrResult
                    }
                  })
                }
              } catch (e) {
                sendError(e)
              }
            }
          })
      `,
        null
      )

      internalState.subProcess.start({
        env: internalState.env,
        directory: engineOptions?.directory,
      })

      internalState.subProcess.process!.stdout?.on(`data`, data => {
        internalState.stdOutListeners.forEach(listener => listener(data))
      })

      internalState.subProcess.process!.stderr?.on(`data`, data => {
        internalState.stdErrListeners.forEach(listener => listener(data))
      })

      internalState.subProcess.onMessage(message => {
        internalState.messageListeners.forEach(listener => listener(message))
      })

      internalState.subProcess.onExit(exitCode => {
        state.exitCode = exitCode || undefined

        const proc = internalState.subProcess?.process

        if (proc?.killed || typeof exitCode === `number`) {
          state.exited = true
        }

        // @ts-ignore if subProcess is accessed from outside it will be recreated automatically
        internalState.subProcess = null

        if (typeof exitCode === `number` && exitCode !== 0) {
          console.error(
            `[content-engine] Process stopped with exit code ${exitCode}`
          )
        }
      })

      if (!internalState.processListenersWereSet) {
        internalState.processListenersWereSet = true
        interface ShutdownServicesOptions {
          subProcess: ControllableScript
          code?: number | null
        }

        function shutdownService(
          {
            subProcess,
            code,
          }: Partial<ShutdownServicesOptions & typeof internalState>,
          signal: NodeJS.Signals
        ): Promise<void> {
          if (!subProcess) {
            return Promise.resolve()
          }

          return subProcess
            .stop(signal, code || 0)
            .catch(() => {})
            .then(() => {})
        }

        process.on(`message`, msg => {
          internalState.subProcess?.send(msg)
        })

        process.on(`SIGINT`, async () => {
          await shutdownService(internalState, `SIGINT`)

          process.exit(0)
        })

        process.on(`SIGTERM`, async () => {
          await shutdownService(internalState, `SIGTERM`)

          process.exit(0)
        })

        onExit((code, signal) => {
          shutdownService(
            {
              subProcess: internalState.subProcess,
              code,
            },
            signal as NodeJS.Signals
          )
        })
      }

      reporter.verbose(`[content-engine] waiting for subprocess to start`)
      const isRunning = await onceReceived(`CONTENT_ENGINE_CHILD_RUNNING`)

      return {
        isRunning,
      }
    }

    return {
      isRunning: true,
    }
  }

  const sendFrameworkContext = (context: FrameworkContext) => {
    if (internalState.subProcess) {
      internalState.subProcess.send({
        type: `CONTENT_ENGINE_FRAMEWORK_CONTEXT`,
        context,
      })
    }
  }

  const initialize: ContentEnginePublicAPI["initialize"] = async (
    { clearCache, env, context } = {
      clearCache: false,
    }
  ): Promise<void> => {
    if (env && internalState.subProcess) {
      console.log(
        `[content-engine] restarting process since env vars were passed to initialize`
      )
      await stop()
    }

    if (clearCache) {
      await deleteCache()
    }

    const { isRunning } = await createSubProcessIfNoneExists({
      env: env || engineOptions?.env,
    })

    if (context) {
      sendFrameworkContext(context)
    }

    if (!isRunning) {
      reporter.verbose(`[content-engine] subprocess exited.`)
      // return early if not running. this means the process exited.
      return
    }

    internalState.subProcess!.send({
      type: `CONTENT_ENGINE_CHILD_INITIALIZE`,
    })

    const result = await onceReceived(
      `CONTENT_ENGINE_CHILD_FINISHED_INITIALIZING`
    )

    if (result?.error) {
      const newErr = new Error(result.error.message)
      newErr.stack = result.error.stack

      throw newErr
    }
  }

  const deleteCache = async (): Promise<void> => {
    await stop()
    const cachePath = path.join(engineOptions!.directory!, `.cache`)

    if (existsSync(cachePath)) {
      await rm(cachePath, {
        recursive: true,
        force: true,
      })
    }

    await getStore().resetCache()
  }

  const sync: ContentEnginePublicAPI["sync"] = async (
    {
      clearCache = false,
      runServer,
      webhookBody,
      env,
      context,
      buildSchema = true,
      connector,
    } = {
      buildSchema: true,
    }
  ): Promise<SyncState> => {
    state.error = undefined

    if (runServer && !buildSchema) {
      throw new Error(
        `Cannot run server without building schema. Set "runServer: true" or "buildSchema: false" to fix this.`
      )
    }

    reporter.verbose(`[content-engine] sync, ${JSON.stringify(env, null, 2)}`)

    if (
      env &&
      internalState.subProcess &&
      JSON.stringify(env) !== JSON.stringify(internalState.env)
    ) {
      console.log(
        `[content-engine] restarting process since new env vars were passed to sync`
      )
      // eslint-disable-next-line @typescript-eslint/no-use-before-define
      await stop()
    }

    if (clearCache) {
      await deleteCache()
    }

    const { isRunning } = await createSubProcessIfNoneExists({
      env: env || engineOptions?.env,
    })

    if (context) {
      sendFrameworkContext(context)
    }

    if (!isRunning) {
      reporter.verbose(`[content-engine] subprocess exited.`)
      // return early if not running. this means the process exited.
      return state
    }

    internalState.subProcess!.send({
      type: `CONTENT_ENGINE_CHILD_SYNC_DATA`,
      payload: {
        runServer,
        webhookBody,
        buildSchema,
        connector,
      },
    })

    reporter.verbose(`[content-engine] waiting for subprocess to finish sync`)
    const { error } =
      (await onceReceived(`CONTENT_ENGINE_CHILD_FINISHED_SYNCING_DATA`)) || {}

    if (error) {
      state.error = {
        message: error.message,
        stack: error.stack,
      }
      reporter.verbose(
        `[content-engine] subprocess finished syncing with errors.`
      )
    } else {
      reporter.verbose(`[content-engine] subprocess finished sync`)
    }

    return { ...state, error: state.error ? { ...state.error } : undefined }
  }

  const restart: ContentEnginePublicAPI["restart"] = async (
    { env, clearCache, buildSchema = true, ...options } = {
      buildSchema: true,
    }
  ): Promise<SyncState> => {
    await stop()

    if (clearCache) {
      await deleteCache()
    }

    console.log(`[content-engine] restarting subprocess`)

    await createSubProcessIfNoneExists({
      env,
    })

    return sync({ ...options, buildSchema })
  }

  const stop = async (
    signal?: NodeJS.Signals,
    code: number = 0
  ): Promise<void> => {
    if (internalState.subProcess) {
      console.log(`[content-engine] stopping subprocess`)

      // if a debugger is attached, the process must be killed with SIGKILL or it will hang indefinitely
      const inDebugMode = inspector.url() !== undefined
      const exitSignal = inDebugMode ? `SIGKILL` : signal || null

      await internalState.subProcess.stop(exitSignal, code)

      // @ts-ignore if subProcess is accessed from outside it will be recreated automatically
      internalState.subProcess = null
    }
  }

  async function deferTestUtilToSubProcess(
    utilName: string,
    args: Array<any>
  ): Promise<any> {
    if (!internalState.subProcess) {
      throw new Error(
        `contentEngine().test.${utilName}() can only be called when content engine is running.`
      )
    }

    // @ts-ignore
    const messageId = crypto.randomUUID()

    internalState.subProcess.send({
      type: `CONTENT_ENGINE_CHILD_INVOKE_TEST_UTIL`,
      payload: {
        args,
        messageId,
        utilName,
      },
    })

    const message = await onceReceived(
      `CONTENT_ENGINE_CHILD_INVOKE_TEST_UTIL_RESULT--${messageId}`
    )

    if (message?.error) {
      if (message.error.startsWith(`Error:`)) {
        message.error = message.error.replace(`Error: `, ``)
      }

      const newErr = new Error(message.error.message)
      newErr.stack = message.error.stack

      throw newErr
    }

    return message?.result
  }

  function getStore(): IDataStore {
    internalState.store ||= getLmdbStore({
      dbPath: getDefaultDbPath(engineOptions?.directory),
    })
    return internalState.store
  }

  const engineApi: ContentEnginePublicAPI = {
    initialize,
    sync,
    restart,
    stop,
    config: async (newConfig: ContentEngineConfiguration) => {
      const normConfig = {
        directory: newConfig.directory || process.cwd(),
        ...(newConfig || {}),
      }

      if (JSON.stringify(normConfig) !== JSON.stringify(engineOptions)) {
        reporter.info(
          `[content-engine] restarting process since config changed`
        )
        await stop()

        const newEngine = contentEngine(normConfig)

        // update the engineApi with the new engine's methods
        // so that calling engine.sync() will use the new engine config instead of the old one
        Object.keys(engineApi).forEach(key => {
          engineApi[key] = newEngine[key]
        })

        return newEngine
      } else {
        reporter.verbose(
          `[content-engine] reusing existing engine since the config didn't change`
        )
        return engineApi
      }
    },

    test: throwOutsideTestEnv({
      getNodes: () => deferTestUtilToSubProcess(`getNodes`, []),
      getNodesByType: (...args) =>
        deferTestUtilToSubProcess(`getNodesByType`, [...args]),
      getNode: (...args) => deferTestUtilToSubProcess(`getNode`, [...args]),
      query: (...args: Array<any>) =>
        deferTestUtilToSubProcess(`query`, [...args]),
    }),

    store: {
      getNode: id => getStore().getNode(id),
      getTypes: () => getStore().getTypes(),
      countNodes: type => getStore().countNodes(type),
      getNodes: () => getStore().iterateNodes(),
      getNodesByType: type => getStore().iterateNodesByType(type),
      runQuery: args => {
        process.env.GATSBY_EXPERIMENTAL_LMDB_INDEXES ||= `1`
        return getStore().runQuery(args)
      },
    },

    getProcess(): ChildProcess {
      createSubProcessIfNoneExists({
        env: internalState.env,
      })

      return internalState.subProcess!.process!
    },

    onStdOut(callback: (data: string) => void): void {
      internalState.stdOutListeners.push(callback)
    },
    onStdErr(callback: (data: string) => void): void {
      internalState.stdErrListeners.push(callback)
    },
    onMessage(callback: (message: any) => void): void {
      internalState.messageListeners.push(callback)
    },
    sendMessage(message: any): void {
      if (internalState.subProcess) {
        internalState.subProcess.send(message)
      } else {
        throw new Error(
          `contentEngine().sendMessage() can only be called when content engine is running.`
        )
      }
    },
    clearListeners(): void {
      internalState.stdErrListeners = []
      internalState.stdOutListeners = []
      internalState.messageListeners = []
    },
  }

  return engineApi
}

export function throwOutsideTestEnv<T>(fns: Record<string, any>): T {
  return Object.fromEntries(
    Object.entries(fns).map(([name, fn]: any) => [
      name,
      (...args) => {
        if (process.env.NODE_ENV !== `test`) {
          throw new Error(
            `contentEngine().test.${name}() can only be called from within tests.`
          )
        }

        return fn(...args)
      },
    ])
  )
}
